import { createRoot } from 'react-dom/client';
import { ConnectionFailed } from './ConnectionFailed';

const connectionFailedRoot = document.createElement('div');
connectionFailedRoot.id = 'connection-failed-root';
createRoot(connectionFailedRoot).render(<ConnectionFailed />);

export function showConnectionFailed(): void {
  renderConnectionFailed();
  const observer = new MutationObserver(renderConnectionFailed);
  observer.observe(document.body, {
    childList: true,
    subtree: true,
  });
}

export function hideConnectionFailed(): void {
  const container = document.getElementById('connection-failed-root');
  if (container) {
    container.remove();
  }
}

function renderConnectionFailed(): void {
  const container = document.getElementById('page-manager');
  if (container && container.childNodes.length > 1) {
    container.replaceChildren(connectionFailedRoot);
  }

  const statusBanner = document.querySelector('ytd-network-status-banner');
  if (statusBanner instanceof HTMLElement && !statusBanner.hidden) {
    statusBanner.hidden = true;
  }
}
