import Icon from '../../../../public/icons/icon128.png';
import { useConfigsBaseUrl } from 'src/utils/hooks';

export function ConnectionFailed(): JSX.Element {
  const { configsUrl } = useConfigsBaseUrl();

  const helpUrl = new URL(process.env.HELP_URL, configsUrl);

  return (
    <div className="connection-failed-container">
      <div className="connection-failed-image-container">
        <img src={Icon} alt="Icon" />
      </div>
      <h2>{process.env.EXTENSION_TITLE} не смог настроить подключение</h2>
      <p>
        Попробуйте переустановить расширение или обратитесь{' '}
        <a
          className="yt-core-attributed-string__link yt-core-attributed-string__link--call-to-action-color"
          href={helpUrl.toString()}
          target="_blank"
          rel="noreferrer"
        >
          к странице помощи
        </a>
      </p>
    </div>
  );
}
