import { Actions } from '../../app/actions/shared';
import { type Message } from '../../app/messages/shared';
import {
  SessionStorageKey,
  getSessionStorageData,
} from '../../app/storage/session/shared';
import {
  hideConnectionFailed,
  showConnectionFailed,
} from '../components/ConnectionFailed';
import { Events, type ShowPageLoaderEvent } from '../../app/events';

chrome.runtime.onMessage.addListener((message: Message) => {
  try {
    if (message.action === Actions.NetworkConnectionRetry) {
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: {
            isShown: true,
          },
        }),
      );
    }
  } catch (error) {
    console.error('error', error);
  }
});

chrome.runtime.onMessage.addListener((message: Message) => {
  try {
    if (message.action === Actions.NetworkConnectionRetrySuccess) {
      hideConnectionFailed();
      window.location.reload();
    }
  } catch (error) {
    console.error('error', error);
  }
});

chrome.runtime.onMessage.addListener((message: Message) => {
  try {
    if (message.action === Actions.NetworkConnectionRetryFail) {
      showConnectionFailed();
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: { isShown: false },
        }),
      );
    }
  } catch (error) {
    console.error('error', error);
  }
});

document.addEventListener('DOMContentLoaded', async () => {
  try {
    const { isNetworkError, connectRetryFailed } = await getSessionStorageData([
      SessionStorageKey.IsNetworkError,
      SessionStorageKey.ConnectRetryFailed,
    ]);
    if (isNetworkError && !connectRetryFailed) {
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: {
            isShown: true,
          },
        }),
      );
    } else if (isNetworkError && connectRetryFailed) {
      showConnectionFailed();
    }
  } catch (error) {
    console.error('error', error);
  }
});
